/* ========================================================================
   #banner
   ======================================================================== */

/**
 * Background-image @mixin is imported, file naming format ends with -sm, -md, 
 * -lg include image name and file type, i.e.
 *
 * File name: image-sm.jpg, image-md.jpg, image-lg.jpg
 *
 * .example {
 *   @include background-image("image-name", jpg);
 * }
 *
 */



//Import banner mixin 
@import "../tools/hero-block";



//Banner 
//Background image
.ipl-banner--wrapper {
  background: colour(white);
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 0;
  content: "";
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-top: spacing(lg); 
}

//Magic numbers used for static image placement
.ipl-banner--rings {
  right: -5%;
  margin-right: auto;
  margin-left: auto;
  opacity: .25;
  position: relative;
  top: spacing(xl);
  z-index: z(base);

  @include mq($from: mobile-wide) {
    left: spacing(-xxxxl);
    top: spacing(sm);
  }

  @include mq($from: tablet) {
    right: 10%;
    top: 0;
  }

  @include mq($from: tablet-wide) {
    opacity: .5;
  }
    
  &:after {
      background-image: url("data:image/svg+xml;base64,");
    background-position: center 0;
    background-repeat: no-repeat;
    background-size: cover;
    content: "";
    height: 700px;
    position: absolute;
    width: 710px;
  }
}

.ipl-banner {
    align-items: center;
    display: flex;
    padding-bottom: spacing(xxs);
    padding-top: spacing(xl);
    position: relative;
}

//Scroll down icon
.ipl-scroll_wrapper {
  display: none;
  z-index: z(footer);

  @include mq($from: tablet ) {
    display: block;
  }  
}

.ipl-scroll,
.ipl-scroll__hump {
  bottom: spacing(-lg);
  font-size: rfs(2.5rem);
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  z-index: z(module);
}

.ipl-scroll__hump {
  bottom: -3px;
  width: 235px;
  z-index: z(footer);
}


//Overlay to meet contrast ratio standards for title, synopsis and buttons
.ipl-banner__overlay {
  background-color: colour(black);
  opacity: 0.3;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: z(base);
}

// Banner content
.ipl-banner__content {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  margin-left: spacing(sm);
  padding: spacing(sm);
  position: relative;
  color: colour(white);
  width: 100%;
  z-index: z(footer);
}

.ipl-banner__description--wrapper {
  display: flex;
  align-content: center;

  .ipl-banner__description {
    margin: 0 spacing(md);
    text-align: left;
  }
  
}



.ipl-tagline,
.ipl-tagline--white {
  align-items: flex-start;
  color: colour(grey-900);
  display: flex;
  flex-direction: column;
  @include font(primary, bold);
  font-size: rfs(2.0rem);
  margin: 0 0 spacing(md);

  &--white {
    color: colour(white);
  }

}

.ipl-gradient,
.ipl-gradient--blue,
.ipl-gradient--orange {
  box-shadow: shadow(xs);
  padding: spacing(xxs) spacing(sm);
  
  &:first-of-type {
    margin-bottom: spacing(xxs);
  }


  &--blue {
    background: colour(blue-200);
    background: gradient(colour(blue-200), colour(blue-100), 30deg);
  }
  &--orange {
    background: colour(orange-200);
    background: gradient(colour(orange-200), colour(orange-100), 30deg);
    
  }
}






.ipl-banner__subtitle {
  margin-bottom: 0;
}

.ipl-banner__synopsis {
  color: colour(grey-900);
  @include font(secondary, regular);
  font-size: rfs(1.125rem);
  margin-bottom: spacing(xl);
  max-width: 50ch;

  @include mq($from: phablet) {
    max-width: 55ch;
  }
}

.ipl-banner__visual__group {
  display: none;
  opacity: 0;

  @include mq($from: phablet) {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: spacing(md);
  opacity: 1;
  position: relative;
  }
}

.ipl-banner__visual--wrapper {
  bottom: 0;
  left: 0;
  right: 0;
  top: 10vh;
  margin: auto;
  max-width: 40vh;
  position: absolute;
} 
    

.ipl-banner__image {
    display: none;

   @include mq($from: mobile) {
    display: flex;
    margin-right: spacing(md);
    max-width: 400px;
    width: 100%;
  }

}

