/* ==========================================================================
   #Feature elements
   ========================================================================== */

/** 
* Highlighted features 
*
* Currently supported by product illustrations 
*/

.ipl-features {
    margin-top: spacing(xxl);
  }

.ipl-feature-list {
    border-spacing: 0; 
    list-style: none;
    margin: 0 0 0 spacing(xs); 
    padding: 0;
    display: flex;  /* [1] */
    flex-wrap: wrap; /* [1] */
    position: relative;
  }
    
  //Card
  .ipl-feature {
    background-clip: padding-box; /* [3] */
    border: 0 solid transparent; /* [2] */
    border-width: 0 20px 20px 0; /* [2] */
    margin: 0;
    padding: spacing(xs) 0 0;
    position: relative;
    width: calc(100% / 2);
  

    @include mq($from: tablet) {
        padding-right: spacing(md);
        padding-left: spacing(md);
        width: calc(100% / 3);
      }

  }
  
  
  //Image
  .ipl-feature--image {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    position: relative;
  }
  
  .ipl-feature--image > * {
    display: block;
    max-width: 100%;
  }
  
  
  //Title
  .ipl-feature--title {
    @include font(primary, bold);
    margin: spacing(sm) 0 spacing(xs);
  }
  
  
  //Content
  .ipl-feature--content {
    flex-grow: 1;
    justify-content: space-between;
    margin-top: spacing(sm);
    padding: 0;
  
    & > *:nth-last-child(2) {
      margin-top: spacing(xs);
      margin-bottom: auto;
    } 
  
  }
  
  .ipl-feature--centre-align {
    text-align: center;
  } 
  
  .ipl-feature--text {
    color: colour(neutral-300);
    display: none;
    font-size: rfs(1rem);
    font-weight: normal;
    line-height: 1.5;
    margin: spacing(sm) 0 spacing(sm);
    max-width: 65ch;

    @include mq($from: mobile-wide) {
      display: block;
    }
  }
  
  .ipl-feature--link {
    border-bottom: none;
    box-shadow: inset 0 0 0 1px colour(grey-500);
    color: colour(link-primary);
    display: flex;
    font-size: rfs(1.25rem);
    height: 100%;
    padding: spacing(sm);
    text-decoration: none;
    width: 100%;
  
    &:hover:not(:focus) {
      border: none;
      box-shadow: inset 0 0 0 3px colour(neutral-300);
      color: colour(link-primary-hover);
      outline: none;
    }
  
    //Full card selection
      &:after {
        bottom: 0; 
        content:'';
        left:0;
        position: absolute;
        right:0;
        top: 0;
      }
    
  }