/* ==========================================================================
   #Side bar navigation
   ========================================================================== */

 /**
  *  A vertical list of links for site navigation typically placed next to 
  *  the body content.
  * 
  *
  */

  //Sidebar navigation
.ipl-pane__sidenav {
  position: relative;

  @include mq($from: tablet) {
    position: sticky;
    top: 10px;
  }

}

.ipl-sidenav {
  display: none;
  font-size: 0;
  line-height: 1.67;
  margin-top: spacing(sm);
  padding-right: spacing(md);


  @include mq($from: tablet) {
    display: flex;
    flex-direction: column;
  }
  
}

.ipl-sidenav__header {
  display: flex;

  .ipl-link {
    border-bottom: none;
    box-shadow: 0 -2px transparent,0 3px colour(neutral-300);
    color: colour(neutral-300);
    line-height: 1.67;
    outline: 2px solid transparent;
    padding: 0;
    text-decoration: none;

    &:hover:not(:focus) {
      border-bottom: none;
    }

  }
  
}

.ipl-sidenav__section {
  list-style-type: none;
  margin: 0 0 spacing(md);
  padding: 0;

  & [aria-current] {
    border-left: 4px solid colour(link-primary);
    font-weight: 600;       
    margin-left: spacing(-md);
    padding: 0 spacing(xs);
  }

}

.ipl-sidenav__section--item {
  margin-top: spacing(xs);
  margin-bottom: spacing(md);
}

.ipl-sidenav__link {
  border-bottom-color: transparent;
  font-size: 1.125rem;
  padding-bottom: .125vh;
}

.ipl-sidenav__link:not(:focus):hover, 
.ipl-sidenav__button:hover {
  color: colour(link-primary);
  border-bottom-color: colour(link-primary);
  border-bottom-width: 2px;
}
