/* ==========================================================================
   #Accordion
   ========================================================================== */

  /**
   * Use accordions to provide a show/hide option for sections of content on 
   * complex pages.
   * 
   *
   * Avoid using an accordion for content that is essential to all users. 
   * Accordions hide content from users and prevent it from being found with 
   * in-page (CTRL-F) search.
   *
   * Reference: https://www.hassellinclusion.com/blog/accessible-accordions-part-2-using-details-summary/
   *
   */

    .ipl-accordion{
      display: flex;
      flex-direction: column;
      margin-top: spacing(lg);
      margin-bottom: spacing(lg);
    }

    .ipl-accordion__controls {
      border-bottom: 1px solid colour(grey-500);
      display: flex;
      justify-content: flex-end;
   
    .ipl-accordion__toggle {
      background: none;
      border-radius: 0;
      border-width: 0;
      border-bottom: none;
      box-shadow: 0 -2px transparent, 0 2px colour(link-primary);
      color: colour(link-primary);
      cursor: pointer;
      font-size: rfs(1.125rem);
      line-height: 1.65;
      margin-bottom: 0;
      padding: 0;
      z-index: z(footer);

      &:hover:not(:focus) {
        border: none;
        box-shadow: 0 -2px transparent, 0 3px colour(link-visited);
        color: colour(link-visited);
      }

      &:focus-visible:not(:focus) {
        outline: 3px solid transparent;
        text-decoration: none;
      }

    }

  }
    
    .ipl-accordion__details {
      border-bottom: 1px solid colour(grey-500);
      color: colour(neutral-300);
      padding: 0;

      & > summary::-webkit-details-marker {
        display: none;
      }

      &[open] > .ipl-accordion__summary span::before {
        height: 0;
      }

    }

    .ipl-accordion__summary {
      cursor: pointer;
      list-style: none;
      margin: spacing(xs) 0;
      padding: spacing(xs) 0;
      position: relative;

      & > * {
        display: inline;
      }

      & .lead {
        display: block;
        margin: spacing(xs) spacing(lg) 0 0;
        max-width: 60ch;
      }

      &:focus:not(:hover) h2 {
        background-color: colour(focus);
        border-bottom: none;
        box-shadow: 0 -2px colour(focus), 0 4px colour(link-visited);
        color: colour(link-visited);
        outline: 3px solid transparent;
        text-decoration: none;
      }

      &:focus-visible {
        outline: 0;
      }

      &:hover:not(:focus) h2 {
        box-shadow: 0 -2px transparent, 0 2px colour(neutral-300);
        cursor: pointer;
        outline: none;
      }

      &::-webkit-details-marker {
        display: none;
      }

    }

    .ipl-accordion__icon {
      height: 1rem;
      left: 0;
      margin-top: 1px;
      position: absolute;
      top: spacing(sm);
      width: 1rem;
  
        &:before, &:after{
        content: "";
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 25%;
        height: 25%;
        margin: auto;
        border: 2px solid transparent;
        background-color: colour(neutral-300);
        }

        &:after {
        width: 100%;
        }    
        &:before {
        height: 100%;
        }    

    }

    .ipl-accordion__title {
      border-bottom-color: transparent;
      font-size: rfs(1.375rem);
      line-height: 1.65;
      margin: 0 spacing(lg);
      padding-bottom: .125vh;
    }

    .ipl-accordion__content {
        margin: spacing(xs) 0;
    }











    