/* ==========================================================================
   #Tabs
   ========================================================================== */

  /**
   * Tabs are used to organize and hide content, so as not to overwhelm the user.
   * Tabs should activate automatically when receiving focus as long as associated 
   * tab panels are rendered quickly.
   *
   * Modifiers line :140
   * If using additional colors edit line(s) :98
   */

  .ipl-tabs {
    margin: spacing(lg) auto spacing(lg);
    max-width: 1180px;
    padding: 0;
  }

  .ipl-tabs__list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    @include mq($from: tablet) {
      flex-direction: row;
    }
    
  }

  .ipl-tabs__list-item{
    display: inline-flex;
    justify-content: center;
    margin-bottom: spacing(xxs);

    &:first-of-type {
      margin-bottom: spacing(sm);
    }

    @include mq($from: tablet) {
      margin-right: spacing(xs);
      margin-bottom: 0;

      &:first-of-type {
        margin-bottom: 0;
      }

      &:last-of-type {
        margin-right: 0;
      }
    }


  }

  .ipl-tabs-link {
    background-color: colour(white);
    border-left: 1px solid colour(grey-300);
    border-right: 1px solid colour(grey-300);
    border-bottom: 1px solid colour(grey-300);
    border-top: none;
    cursor: pointer;
    font-size: rfs(24px);
    margin-bottom: spacing(xs);
    opacity: .95;
    padding: spacing(md) spacing(sm);
    position: relative;
    width: 100%;
    text-align: center;
    text-decoration: none;
    top: 1px;
    width: 100%;
    z-index: z(base);

    &:first-of-type {
      margin-bottom: 0;

      @include mq($from: tablet) {
        margin-bottom: spacing(xs);
      }
    }

    //Unselected state colors
    &.ipl-tabs__primary { 
      box-shadow: 0px -8px 0px 0px colour(grey-500), 
      0px -2px 1px 0px rgba(0,0,0,0.1), 
      -2px 5px 0px -2px rgb(255, 255, 255), 
      0px -2px 1px 0px rgba(0,0,0,0.1);

      @include mq($from: tablet) {
        box-shadow: 0px -8px 0px 0px colour(orange-200), 
        0px -2px 1px 0px rgba(0,0,0,0.1), 
        -2px 5px 0px -2px rgb(245, 245, 245), 
        0px -2px 1px 0px rgba(0,0,0,0.1);
      }
    }
    &.ipl-tabs__secondary {
      box-shadow: 0px -8px 0px 0px colour(grey-500), 
      0px -2px 1px 0px rgba(0,0,0,0.1), 
      -2px 5px 0px -2px rgb(255, 255, 255), 
      0px -2px 1px 0px rgba(0,0,0,0.1);

      @include mq($from: tablet) {
        box-shadow: 0px -8px 0px 0px colour(blue-200), 
        0px -2px 1px 0px rgba(0,0,0,0.1), 
        -2px 5px 0px -2px rgb(245, 245, 245), 
        0px -2px 1px 0px rgba(0,0,0,0.1);
      }
    }


    //Selected state
    &[aria-selected] {
      opacity: 1;

      &.ipl-tabs__primary { 
        box-shadow: 0 -12px 0 0 colour(orange-200), 
                    0 -2px 1px 0 rgba(0,0,0,.1),
                    -2px 5px 0 -2px rgb(255, 255, 255), 
                    0 -2px 1px 0 rgba(0,0,0,.1);
      }
      &.ipl-tabs__secondary { 
        box-shadow: 0 -12px 0 0 colour(blue-200), 
        0 -2px 1px 0 rgba(0,0,0,.1),
        -2px 5px 0 -2px rgb(255, 255, 255), 
        0 -2px 1px 0 rgba(0,0,0,.1);
      }

      @include mq($from: tablet) {
        border-bottom: 0;
        margin-bottom: 0;
      }
     }

    .ipl-tabs-text {
      color: colour(grey-900);
      font-size: rfs(24px);
    }

    //focus override
    &:focus-visible {
      outline: 3px solid colour(focus)!important;//override box-shadow used for tab-header
      outline-offset: -3px;
    }
  }

  .ipl-tabs__section {
    background-color: colour(white);
    border: 1px solid colour(grey-300);
    box-shadow: shadow(xs);
    padding: spacing(lg);
  }

.ipl-tabs__media {
  margin: spacing(sm);
  padding: 0;
}

.ipl-tabs__media--group {
  display: flex;
  flex-direction: column;

  @include mq($from: tablet) {
    justify-content: space-between;
    flex-direction: row;
  }
  .ipl-grid--one-half:first-of-type {
    margin-bottom: spacing(sm);
  }
}


//Modifiers 

//Full width
.ipl-tabs__full-width > li {
    flex-grow: 1;
 }

 //Large
 .ipl-tabs-link__large > .ipl-tabs-link {
  padding: spacing(lg) spacing(sm);
 }

 //Small
 .ipl-tabs-link__small > .ipl-tabs-link {
  padding: spacing(sm) spacing(xs);
}



  
