
/* ==========================================================================
   #Overflow menu
   ========================================================================== */

/**
  *  Use the overflow menu component when additional options are available to the 
  *  user but there are space constraints.
  * 
  */

  .ipl-overflow {
    margin-right: spacing(xs);

    @include mq($from: mobile-wide) {
      margin-right: 0;
    }

    &:hover {
       text-decoration: none;
    }

  }
  
  .ipl-overflow__btn {
    background-color: transparent;
    border-color: transparent;
    border-width: 1px;
    box-shadow: none;
    color: colour(neutral-300);
    cursor: pointer;
    display: flex;
    font-family: inherit;
    height: 100%;
    margin: 0;
    outline: none;
    overflow: hidden;
    padding: spacing(xxxs);
    position: relative;
    text-decoration: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 100%;



    .ipl-overflow__menu {
        font-size: rfs(3.5rem);
    }

    @include mq($from: mobile-wide) {
        display: none;
    }

  }

  .ipl-overflow__content {
    background-color: colour(white);
    border: 1px solid colour(grey-400);
    border-radius: radius(xxxs);
    box-shadow: shadow(md);
    display: none;
    flex-direction: column;
    position: absolute;
    right: 2.5rem;
    padding: spacing(xs) spacing(sm);
    z-index: z(footer); 

    &::before {
      background-color: colour(white);
      border-top: 1px solid colour(grey-400);
      border-left: 1px solid colour(grey-400);
      content: "";
      display: block;
      height: .5rem;
      position: absolute;
      right: spacing(sm);
      top: -.275rem;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg);
      width: .5rem;
      z-index: z(negative);

      @include mq($from: mobile-wide) {
        display: none;
      }
    }



    &.active {
      display: flex;
      z-index: z(overlay); 


    }

    @include mq($from: mobile) {
      right: spacing(xxxxl);
    }

    .ipl-btn {
        justify-content: center;
        margin: spacing(xxxs) 0 spacing(xxs) 0;
        width: 100%;

        &:first-of-type {
          margin-bottom: spacing(sm);
        }

        @include mq($from: mobile-wide) {  
          margin: 0 spacing(sm) 0 0;
          width: auto;

          &:first-of-type {
            margin-bottom: 0;
          }
        }
    }

    @include mq($from: mobile-wide) {   
        align-items: center;
        background-color: transparent;
        border: none;
        box-shadow: none;
        display: flex;
        flex-direction: row;
        margin-top: 0;
        min-width: auto;
        padding: 0;
        position: relative;
        right: 0;
    }

    .ipl-divider--vertical-light {
        display: none;

        @include mq($from: mobile-wide) { 
            display: flex;
            margin-right: spacing(sm);
            margin-left: 0;
            max-height: 1.9rem;
        }

    }
    
  }
  
  

