/* ==========================================================================
   #Alert
   ========================================================================== */

  /**
   * Component to show a warning, success or information at the top of the page.
   *
   */
  

.ipl-alert__dismiss {
  color: colour(white);
}

.ipl-global-alert {
    display: block;
    position: fixed;
    width: 100%;
    z-index: z(overlay);

    &.ipl-error {
      background: colour(error);
      color: colour(white);
    }

    &.ipl-success {
      background: colour(success);
      color: colour(white);
    }
  
    &.ipl-info {
      background: colour(info);
      color: colour(white);
    }

  }

  .ipl-alert__content {
    display: flex;
    padding: spacing(sm) 0;
    position: relative;
    margin: 0;
}

.ipl-alert__icon--wrapper {
  display: flex;
}

.ipl-alert__icon {
  align-self: center;
  display: flex;
  font-size: 2rem;
  min-width: 2rem;
}

.ipl-alert__text {
  color: colour(white);
  @include font(secondary, medium);
  font-size: rfs(1rem);
  line-height: 1.5;
  margin: 0 spacing(md) 0 spacing(sm);

  @include mq($from: tablet) {
    font-size: rfs(1.125rem);
    margin: 0 spacing(sm);
  }
}

.ipl-alert__close {
  align-self: center;
  background: transparent;
  border: 0;
  cursor: pointer;
  font-size: rfs(1.75rem);
  height: 2rem;
  outline: 0;
  padding: 0;
  position: relative;
  right: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 2rem;  

  @include mq($from: phablet) {
    position: absolute;
  }

  &:focus:not(:hover), 
  &:focus-visible {
    border-color: colour(link-visited);
    border-width: 1px;
    box-shadow: inset 0 0 0 3px colour(link-visited);
    outline: 3px solid colour(focus);
    outline-offset: 0;
  }
}


