/* ==========================================================================
   #Search
   ========================================================================== */

  /**
   * Search input value is set to accomodate the standard user query length - 
   *  typically about 27 characters 
   *
   *
   * ref: https://www.algolia.com/blog/3-key-ux-design-elements-of-the-search-bar/
   *
   * Search results - :115
   */
   
 //Desktop wrapper
#ipl-search__wrapper {
  display: flex;
}

.ipl-search__actions {
  display: block;

  @include mq($from: tablet) {
    display: none;
  }
}

.ipl-search__header, 
.ipl-search__widget {
  align-items: center;
  display: flex;  
  justify-content: center;
  position: relative;
  width: 100%;

  
  @include mq($from: mobile-wide) {
    width: 35ch;
  }

  @include mq($from: tablet) {
    width: 100%;
  }


  // @include mq($from: mobile-wide) {
  //   width: auto;
  // }
  
}

.ipl-search[role=search],
.ipl-search-listing[role=search] {
  display: flex;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
  z-index: z(footer);
}

.ipl-search__item,
.ipl-search-listing__item {
  background-color: colour(white);
  display: flex;
  position: relative;
  width: 100%;

  @include mq($from: mobile-wide) {
    width: auto;
  }

}

.ipl-search__label {
  width: 100%;
}

.ipl-search__input,
.ipl-search-listing__input {
  border: 1px solid colour(blue-200);
  border-radius: 50px;
  -webkit-appearance: none;
  box-sizing: border-box;
  color: colour(neutral-300);
  line-height: 1.5;
  padding: spacing(xs) spacing(sm);
  width: 100%;


  &:focus{
    box-shadow: inset 0 0 0 3px colour(link-visited);
    outline: 3px solid colour(focus);
    outline-offset: 0;
    border-color: colour(link-visited);
    border-width: 1px;
  }
}

.ipl-search__input {
  @include input-placeholder {
    color: colour(grey-500);
    font-size: 1.1rem;
  }
}

.ipl-search__button {
  align-items: center;
  background: transparent url("data:image/svg+xml;charset=utf-8,%3Csvg width='24' height='24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24 21.646l-6.255-6.205a9.63 9.63 0 001.872-5.711c0-5.365-4.4-9.73-9.809-9.73C4.4 0 0 4.365 0 9.73c0 5.366 4.4 9.73 9.808 9.73a9.81 9.81 0 005.531-1.698L21.627 24 24 21.646zM2.877 9.73c0-3.792 3.11-6.877 6.932-6.877 3.822 0 6.932 3.085 6.932 6.877s-3.11 6.877-6.932 6.877c-3.823 0-6.932-3.085-6.932-6.877z' fill='currentColor'/%3E%3C/svg%3E") no-repeat 50%;
  border: none;
  border-radius: 0;
  color: colour(black);
  cursor: pointer;
  display: flex;
  height: 1.5rem;
  margin: 0;
  line-height: 1.5;
  padding: spacing(sm);
  position: absolute;
  right: spacing(xs);
  top: 50%;
  transform: translate(0, -50%);
  width: 1.5rem;

  &:focus{
    box-shadow: inset 0 0 0 3px colour(link-visited);
    outline: 3px solid colour(focus);
    outline-offset: 0;
    border-color: colour(link-visited);
    border-width: 1px;
  }
  
}



//Search results
.ipl-title {
  margin-bottom: 1.5rem;
}

.ipl-search__results-count {
  font-size: rfs(18px);
  margin-top: spacing(md);
  margin-bottom: spacing(md);

  @include mq($from: tablet-wide) {
    margin-top: spacing(lg);
    margin-bottom: spacing(lg);
  }

}

.ipl-search__result {
  line-height: initial;
  margin: initial;
  padding: initial;
}

.ipl-search__result--item {
  list-style: none;
  margin-bottom: spacing(lg);
  border-top: 1px solid colour(grey-500 );
  padding-top: spacing(lg);

  &:last-of-type {
    border-bottom: 1px solid colour(grey-500 );
    padding-bottom: spacing(lg);
  }
}     

.ipl-search__result--title {
  font-weight: bold;
  margin-bottom: spacing(sm);
  padding: 0;
}

.ipl-search__inline-details {
  color: colour(grey-500);
  font-size: rfs(16px);
  margin-bottom: spacing(sm);
}

.ipl-search-listing__result-summary {
  font-size: rfs(16px);
  margin-bottom: 0;
}



