/* ==========================================================================
   #Pagination
   ========================================================================== */

.ipl-pagination {
  display: flex;
  justify-content: flex-start;
  margin: spacing(lg) 0;
}

.ipl-pagination__list {
  display: flex;
  margin: 0;
  padding: 0;
}
.ipl-pagination__item {
  border-bottom-color: transparent;
  font-size: rfs(18px);
  line-height: 1.5;

  & + .ipl-pagination__item {
    padding-left: spacing(md);
  }

  &.disabled {
    display: none;
  }

}

.ipl-pagination__link { 
  color: colour(link-primary);
  font-size: 1.125rem;
  line-height: 1.5;
  margin: 0 spacing(xs);
  padding: 0 spacing(xs);
  text-align: center;
  text-decoration: none;

  &:not(:focus):hover {
    color: colour(link-primary-hover);
  }

  &:not(:hover):focus {
    background-color: colour(focus);
    border-bottom: 2px solid transparent;
    box-shadow: 0 -2px colour(focus),0 4px colour(link-visited);
    color: colour(link-visited);
    outline: 3px solid transparent;
  }

  //Current link
  &[aria-current] {
    color: colour(neutral-300);
    font-weight: bold;
    position: relative;
    top: -.375rem;
  }

}


//Previous, Next arrows

.ipl-pagination__previous {
  position: relative;

  .ipl-pagination__link {
    margin-left: 0;
    padding-left: 0;

    &::before {
      display: inline-block;
      content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23005dbb' d='M5.2 11.9c0-.4.2-.9.5-1.2L15.9.6c.7-.7 1.7-.7 2.4 0s.7 1.7 0 2.4l-9.1 9 9.1 9.1c.7.7.7 1.7 0 2.4s-1.7.7-2.4 0L5.7 13.1c-.3-.3-.5-.8-.5-1.2z'/%3E%3C/svg%3E");
      height: 1rem;
      position: relative;
      // transform: scaleX(-1);
      margin-right: spacing(sm);
      top: 2px;
      width: 0;
    }

    &:focus,
    &:hover {

      &:before {
          content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23000C48' d='M5.2 11.9c0-.4.2-.9.5-1.2L15.9.6c.7-.7 1.7-.7 2.4 0s.7 1.7 0 2.4l-9.1 9 9.1 9.1c.7.7.7 1.7 0 2.4s-1.7.7-2.4 0L5.7 13.1c-.3-.3-.5-.8-.5-1.2z'/%3E%3C/svg%3E");
      }
    }

  }
  
 }

 .ipl-pagination__next {
  position: relative;

  .ipl-pagination__link {
    margin-right: 0;
    padding-right: 0;

    &::after {
      display: inline-block;
      content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23005dbb' d='M5.2 11.9c0-.4.2-.9.5-1.2L15.9.6c.7-.7 1.7-.7 2.4 0s.7 1.7 0 2.4l-9.1 9 9.1 9.1c.7.7.7 1.7 0 2.4s-1.7.7-2.4 0L5.7 13.1c-.3-.3-.5-.8-.5-1.2z'/%3E%3C/svg%3E");
      height: 1rem;
      position: relative;
      transform: scaleX(-1);
      margin-left: spacing(sm);
      top: 2px;
      width: 1rem;
    }

    &:focus,
    &:hover {

      &:after {
          content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23000C48' d='M5.2 11.9c0-.4.2-.9.5-1.2L15.9.6c.7-.7 1.7-.7 2.4 0s.7 1.7 0 2.4l-9.1 9 9.1 9.1c.7.7.7 1.7 0 2.4s-1.7.7-2.4 0L5.7 13.1c-.3-.3-.5-.8-.5-1.2z'/%3E%3C/svg%3E");
      }
    }


  }

  
  
 }

























