/* ==========================================================================
   #Slider
   ========================================================================== */

 /**
  * Slider based off the accessible card slider 
  * https://accessible360.github.io/accessible-slick/
  * card settings can be adapted in js/scripts.js //Slider
  *
  */


.ipl-slider__wrapper {
   margin-top: spacing(lg);

    @include mq($from: tablet ) {
        margin-top: 0;
      }  
  }

  //Slider menu
.ipl-slider__menu {
    border-top: 1px solid colour(neutral-300);
    display: flex;
    margin: 0;
    overflow-x: auto;
    position: relative;
    width: 100%;
    z-index: z(footer);

    @include mq($from: mobile-wide) {
        overflow-x:unset;
      }

}

.ipl-slider__menu-title {
  margin: spacing(sm) 0 0;
}

.ipl-slider__menu-link {
    align-items: flex-end;
    border-bottom: 3px solid transparent;
    color: colour(grey-900);
    display: flex;
    font-size: rfs(16px);
    margin: spacing(xxxs) spacing(lg);
    text-align: center;
    text-decoration: none;

    &:first-of-type {
      margin-left: 0;
    }
    &:last-of-type {
        margin-right: 0;
    }

    &.active{
      box-shadow: 0 -5px 0 0 colour(neutral-300);
    }

    @include mq($from: tablet-small) {
      text-align: left;
    }

    &:focus-visible {
        outline: 0;
    }
 
}


//Slider
.ipl-slider {
    position: relative;
}

.ipl-slide__list {
    position: relative;
    display: block;
    margin: 0;
    padding: 0;
}

.ipl-slide {
    float: left;
    height: 100%;
    min-height: spacing(xs);
    display: none;
    padding: spacing(sm) 0;
    display: flex;
    height: auto;
    align-items: center;
    justify-content: center;

    @include mq($from: tablet) {
        padding: spacing(xl) 0; 
      }
}

.ipl-slide__list,
.ipl-slider {
    display: block;
    margin-top: spacing(sm);
    position: relative;
}

.ipl-slide__list {
    margin: 0;
    padding: 0;

    &.ipl-slider__draggable {
        cursor: pointer;
        cursor: hand;
    }

}

.ipl-slide__list,
.ipl-slide__track {
    transform: translateZ(0);
}

.ipl-slide__track {
    display: flex;
    left: 0;
    margin-right: auto;
    position: relative;
    top: 0;

    &:after,
    &:before {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
        }
}

.ipl-slide__initialized .ipl-slide {
    display: block;
 }

.ipl-slide__card {
    display: block;
    height: 100%;
    margin: 0;
    text-decoration: none;
}

.ipl-banner__description {
    align-self: center;
}

//Buttons
.ipl-slider__overflow--hidden {
    overflow: hidden;
  }

.ipl-slider__prev-button, 
.ipl-slider__next-button {
  border-top-right-radius: radius(xxl);
  border-bottom-right-radius: radius(xxl);
  background: colour(white) url("data:image/svg+xml;base64,") no-repeat center;
  border: none;
  box-shadow: 0 2px 3px 0 rgba(0,0,0,.45);
  cursor: pointer;
  opacity: .75;
  padding: 1.25rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: z(module);

  @include mq($from: desktop) {
    border-radius: 50%;  
    opacity: 0;
  }
  
    &:focus-visible, 
    &:hover, 
    &:focus:not(:hover) {
        opacity: 1;
    }

}

.ipl-slider__prev-button {
   left: spacing(-lg);

   @include mq($from: desktop-wide) {
    left: spacing(-xxl);     
  }

}

.ipl-slider__next-button {
    right: spacing(-lg);
    transform: scaleX(-1);

    @include mq($from: desktop-wide) {
      right: spacing(-xxl);     
    }
  
}

.ipl-slide__loading .ipl-slide__track {
    visibility: hidden;
}

[dir="rtl"] .ipl-slide {
    float: right;
}

.ipl-slide img {
    display: block;
}

.ipl-slide.ipl-slide__loading img {
    display: none;
}

.ipl-slide.dragging img {
    pointer-events: none;
}

.ipl-slide__loading .ipl__slide {
    visibility: hidden;
}
.ipl-slide__vertical .ipl-slide {
    border: 0.0625rem solid transparent;
    display: block;
    height: auto;
}

