/* ========================================================================
  #Icons
  ======================================================================== */
 
/**
 * Redefine the sprite elements against tools classes to allow for
 * element manipulation and styling, e.g. manipulate colour and scale:
 *
 * .example {
 *   @extend .my--shape;
 *   color: colour(blue-200);
 * }
 *
 * To access icon colours variables are used to embed the colour
 * within brackets: #{var(--variablename)}; e.g.
 *
 * .example {
 *   --colour-name: #{colour(grey-100)};
 * }
 *
 */

//Import rendered sprites, updated when additional elements added to the
//source folder
 @import "../elements/sprites";


//Fill color based on icon color value
[class^="ipl-icon"] > use {
  fill: currentColor;
}


//Primary navigation
.ipl-nav__chevron-down {
  bottom: 0;
  color: colour(neutral-300);
  font-size: 1.375rem;
  margin-top: -1px;
  max-width: 100%;
  position: absolute;
  right: 16px;
  top: 28px;
  transform: translateY(-25%);

  @include mq($from: desktop) {
    margin-top: 0;
    right: -8px;
    top: 50%;
    transform: translateY(-50%);
  }
}

//Mobile menu
.ipl-nav__menu,
.ipl-nav__menu-close {
  font-size: 1.75rem;
}

//CloudPayroll logo
.cpl-logo {
  height: 1.5rem;
  margin: 0 spacing(xxs) 0 0;
  transition: all .25 ease-in-out;
  width: 1.5rem;

  @include mq($from: mobile) {
    margin: 0;
  }

  @include mq($from: desktop) {
    height: 2rem;
    width: 2rem;
  }
}
.cpl-logo--title {
  align-items: end;
  bottom: -5px;
  color: colour(neutral-300);
  display: none;
  @include font(primary, bold);
  font-size: 1.25rem;
  margin-left: spacing(xs);
  position: relative;

  @include mq($from: mobile) {
    bottom: -4px;
    display: flex;
    margin-left: spacing(xs);
  }

  sub {
    font-size: .25rem;
    margin-left: 3px;
    position: relative;
    top: spacing(-md);

    @include mq($from: mobile) {
      font-size: .375rem;
      margin-left: spacing(xs);
    }
  }

}
.cpl-logo--blue-500{
 display: none;
}


