/* ==========================================================================
   #Checkbox
   ========================================================================== */

  /**
   * Let users select one or more options.	
   *
   * Re-usable styling for checkboxes styles
   *
   */

	 .ipl-checkboxes {
		  margin-top: spacing(md);
	 }

	 .ipl-checkboxes__item {
	  	display: block;
		font-weight: 400;
		font-size: rfs(1.1rem);
		line-height: 1.5;
		min-height: spacing(lg);
		margin-bottom: spacing(md);
		padding-left: spacing(xl);
		position: relative;
	 }

	 .ipl-checkboxes__input {
		cursor: pointer;
		height: 2.25rem;
		left: 0;
		margin: 0;
		opacity: 0;
		position: absolute;
		top: 0;
		width: 2.25rem;
		z-index: z(footer);

      &:focus+.ipl-checkboxes__label:before {
		border-width: 4px;
		box-shadow:0 0 0 3px colour(focus);
        outline: 3px solid transparent;
        outline-offset: 1px;
			}

			&:checked+.ipl-checkboxes__label:after {
				opacity:1
			}

			&:disabled,
			&:disabled+.ipl-checkboxes__label {
				cursor:default;
				opacity:.5;
			}

	 }

	 .ipl-checkboxes__label {
		display: inline-block;
		font-size: rfs(1.125rem);
		line-height: 1.5;
		margin-bottom: 0;
		margin-left: spacing(sm);
		padding: spacing(xxs) 0 spacing(sm);
		cursor: pointer;
		-ms-touch-action: manipulation;
		touch-action: manipulation;

      //box shape
			&:before  {
				background: transparent;
				border: 2px solid currentColor;
				box-sizing: border-box;
				content: "";
				height: spacing(xl);
				position: absolute;
				top: 0;
				left: 0;
				width: spacing(xl);
			}
			
      //tick symbol
			&:after {
				content: "";
				box-sizing: border-box;
				position: absolute;
				left: 7px;
				top: 10px;
				width: 22px;
				height: 12px;
				transform: rotate(-45deg);
				border: solid;
				border-width: 0 0 5px 5px;
				border-top-color: transparent;
				opacity: 0;
				background: transparent;
			}
			
	 }






