/* ==========================================================================
   #Articles
   ========================================================================== */

  /**
   * Articles can be set as columns and/or rows, specifying the numbers
   * accordingly.
   *
   */

.ipl-article-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 0 spacing(lg);
  padding: 0;
  position: relative;

  @include mq($from: tablet-wide) {
    flex-direction: row;
 }

}

.ipl-article {
  display: flex;
  flex-direction: column;
  margin-bottom: spacing(lg);
  position: relative;

  &:last-of-type {
    margin-bottom: 0;
  }

  @include mq($from: tablet-wide) {
    flex-basis: 31%;
    margin-bottom: 0;
  }

}

.ipl-article__title {
  margin-bottom: 0;
  order: 2;
  padding: 0;
}

.ipl-article--link {
  border-bottom: 2px solid colour(link-primary);
  color: colour(link-primary);
  @include font(primary, bold);
  font-size: rfs(1.25rem);
  padding-bottom: .125vh;
  text-decoration: none;

  &:hover:not(:focus) {
    border-bottom: 3px solid colour(link-primary-hover);
    color: colour(link-primary-hover);
    outline: none;
  } 

}

.ipl-article__date {
  font-size: rfs(.875rem);
  order: 1;
}

.ipl-article__summary {
  margin: 0;
  order: 3;
}
