/* ==========================================================================
   #Page Header
   ========================================================================== */

.ipl-header-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: z(overlay); 
}

.ipl-header {
  background-color: colour(white);
  border-bottom: 1px solid colour(grey-400);
  box-shadow: shadow(xs); 
  margin: 0;
  padding: 0;
  position: relative;
}

.ipl-header__wrapper {
  display: flex;

  .ipl-divider--vertical {
    align-self: center;
    border-color: colour(grey-400);
    display: flex;
    margin: 0 spacing(sm);
    max-height: 1.9rem;

    @include mq($from: tablet-wide) {
      display: none;
   }
  }
}

//Header wrapper - desktop & mobile
.ipl-logo__wrapper {
  margin: spacing(sm) 0;

  @include mq($from: tablet-wide) {
    margin: 0;
 }
}

.ipl-logo--primary,
.ipl-mobile__logo-wrapper {
  align-items: center;
  display: flex;
  margin: 0;

  //Adjust divider between hamburder menu & logo
  .ipl-divider--vertical-light {
    height: 1.9rem;
    margin-left: spacing(sm);
    margin-right: spacing(sm);

    @include mq($from: tablet-wide) {
      border: none;
      margin: 0;
   }
  }
}

//Logo settings
.ipl-header__logotype {
  align-items: center;
  border-bottom: none;
  display: flex;
  margin: 0;
  text-decoration: none;

  &:focus {
    background-color: colour(focus);
    box-shadow: 0 -2px colour(focus), 0 4px colour(link-visited);
    color: colour(link-visited);
    filter: unset;
    outline: 3px solid transparent;
    text-decoration: none;

    .cpl-logo, .cpl-logo--title {
      filter: brightness(100) invert(1);
    }
  }

  &:focus-visible {

    .ipl-header__logo { 
      background-color: colour(focus);
      color: colour(link-visited);
      box-shadow: 0 -2px colour(focus), 0 4px colour(link-visited);
      outline: 3px solid transparent;
      padding: 0;
      text-decoration: none;
  
      .ipl-header__logo--image {
        filter: invert(1) brightness(0);
      }
    }
  }

  @include mq($from: tablet-wide) {
    box-shadow: none;
    margin: 0 spacing(xs) 0 0;
    padding: 0;
  }  

}

//Right hand buttons
.ipl-header__actions {
  align-items: center;
  display: flex;
  margin: 0 0 0 auto;
  position: relative;
  z-index: z(base);
}
