/* ==========================================================================
   #Filter
   ========================================================================== */

  /**
   * Filter used to refine search results and/or forms. <details> <summary>
   * used for mobile dropdown.
   *  
   * https://www.scottohara.me/blog/2018/09/03/details-and-summary.html
   * 
   */

.ipl-filter {
  display: flex;
  flex-direction: column;
  font-size: rfs(1rem);
  line-height: 1.5;
  margin-top: 0;
  position: -webkit-sticky;
  position: sticky;
  top: spacing(xs);

  @include mq($from: tablet) {
    padding-right: spacing(sm);
  }

}


.ipl-filter__details {


  & > summary::-webkit-details-marker {
    display: none;
  }

  &[open] > .ipl-filter__summary span::before {
    height: 0;
  }


}


 .ipl-filter__icon {
    display: block;
    height: 1rem;
    left: 0;
    margin-top: spacing(xs);
    position: absolute;
    top: 15%;
    width: 0;

  @include mq($from: tablet) {
    display: none;
  }
   &:before, &:after{
    content: "";
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 25%;
    height: 25%;
    margin: auto;
    border: 2px solid transparent;
    background-color: colour(neutral-300);
   }

   &:after {
    width: 100%;
   }    
   &:before {
    height: 100%;
   }    
 }


.ipl-filter__summary {
  box-shadow: 0 -2px transparent, 0 3px colour(neutral-300);
  cursor: pointer;
  list-style: none;
  margin: spacing(xs) 0 spacing(lg);
  padding: spacing(xs) 0;
  pointer-events: visible;
  position: relative;

  @include mq($from: tablet) {
    box-shadow: none;
    pointer-events: none;
  }

  &:focus-visible {
    outline: 0;
  }

  &:focus:not(:hover) h2 {
    background-color: colour(focus);
    border-bottom: none;
    box-shadow: 0 -2px colour(link-visited), 0 4px colour(link-visited);
    color: colour(link-visited);
    outline: 3px solid transparent;
    text-decoration: none;

    @include mq($from: tablet) {
      background-color: transparent;
      border-bottom: none;
      box-shadow: none;
    }

  }



  .ipl-h3 {
    border-bottom: none;
    color: colour(neutral-300);
    display: inline;
    line-height: 1.67;
    margin: 0 0 spacing(lg) 0;
    outline: 2px solid transparent;
    padding: 0 0 2px spacing(lg);
    text-decoration: none;
    
    @include mq($from: tablet) {
      box-shadow: 0 -2px transparent, 0 3px colour(neutral-300);
      padding-left: 0;
    }

  }

}
  
.ipl-filter__section {
  margin: 0 0 spacing(sm);
  padding: 0;

    @include mq($from: tablet) {
      border: none;
      margin: 0;
    }

} 

.ipl-filter__section--items {
  list-style-type: none;
  margin: spacing(lg) 0 0 spacing(xs);
  position: relative;

  @include mq($from: tablet) {
    margin: spacing(xs) 0 spacing(sm) spacing(xs);
  }  

}

.ipl-filter__section--item {
  margin: spacing(xs) 0;
}


.ipl-filter__section--label {
  font-size: rfs(1.125rem);
  padding-left: spacing(xs);
}
  