/* Specific spacing values for elements. Spacing base values are in 
 * ./settings/global.scss
 *
 * Reference: https://css-tricks.com/magic-numbers-in-css/
 *
 * // Sapcing is divided into two sections, HTML values and CSS values
 *
 * // HTML values
 *
 *   E.g.
 *   .ipl-m-sm {}     //margin small
 *   .ipl-pl-lg {}    //padding-left large
 *   .ipl-mr-xxs {}   //margin-right xx-small
 *   .ipl-pr-none {}  //padding-right
 *   .ipl-px-lg {}    //padding-left & padding-right lg
 *   .ipl-my-small {} //margin-top & margin bottom small
 *
 *  
 *
 *   <div class="ipl-px-lg"></div>
 *
 *   Will render as:
 *
 *   .ipl-pt-lg {
 *       padding-left:  1.25em;
 *       padding-right: 1.25em;
 *    }
