/* ==========================================================================
   #Filter
   ========================================================================== */
/**
 * Filter used to refine search results and/or forms. <details> <summary>
 * used for mobile dropdown.
 *  
 * https://www.scottohara.me/blog/2018/09/03/details-and-summary.html
 * 
 */
.ipl-filter {
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  line-height: 1.5;
  margin-top: 0;
  position: -webkit-sticky;
  position: sticky;
  top: 0.75rem;
}
@media (min-width: 48em) {
  .ipl-filter {
    padding-right: 1rem;
  }
}

.ipl-filter__details > summary::-webkit-details-marker {
  display: none;
}
.ipl-filter__details[open] > .ipl-filter__summary span::before {
  height: 0;
}

.ipl-filter__icon {
  display: block;
  height: 1rem;
  left: 0;
  margin-top: 0.75rem;
  position: absolute;
  top: 15%;
  width: 0;
}
@media (min-width: 48em) {
  .ipl-filter__icon {
    display: none;
  }
