/* ==========================================================================
   #Breadcrumbs
   ========================================================================== */

  /**
   * The Breadcrumbs component helps users to understand where they are within 
   * a websites structure and move between levels.
   *
   * In this markup the <nav> element uses an aria-label="breadcrumb" to announce 
   * this as a "breadcrumb navigation" to screen readers. 
   *
   * The aria-current="page" is placed on the last element in the breadcrumb  
   * navigation to append "current page to the announcement of the accessible  
   * name of the link. 
   *
   * e.g. "About, current page"
   *  
   */

   .ipl-pane__breadcrumb {
      width: 100%;
   }

   .ipl-breadcrumb {
      display: block;
   }

    .ipl-breadcrumb__section {
      display: flex;
      flex-wrap: wrap;
      margin-top: spacing(sm);
      margin-left: spacing(xs);
      padding: spacing(sm);
    }

    .ipl-breadcrumb__list-item {   
      font-size: rfs(1rem);
      line-height: 1.67;

      & + .ipl-breadcrumb__list-item {
         padding-left: spacing(sm);
      }

      & + .ipl-breadcrumb__list-item::before {
         color: colour(neutral-300);
         content: "/";
         display: inline-flex;
         padding-right: spacing(xs);
     }

     //Current link
     &[aria-current] {
       color: colour(neutral-300);
     }

     //Hide current link in mobile
     &:last-of-type {
        display: none;

        @include mq($from: tablet) {
         display: block;
       }

     }

    }

   .ipl-breadcrumb__link {
      border-bottom: 2px solid colour(link-primary);
      color: colour(link-primary);
      padding-bottom: spacing(xxxs);
      text-decoration: none;

     &:hover:not(:focus) {
         border-bottom-color: colour(link-primary-hover);
         border-bottom-width: 3px;
         color: colour(link-primary-hover);
     }

   }