/* ==========================================================================
   #Page Footer
   ========================================================================== */

.ipl-footer {
    background-color: colour(neutral-300);
    margin-top: spacing(lg);
    padding: spacing(xs) 0;
    position: relative;
    z-index: z(footer);

    @include mq($from: tablet) {
      padding: spacing(lg) 0 0;
    }

    //full width required for menu
    .ipl-container {
      margin: 0;
      width: 100%;

      @include mq($from: tablet) {
        margin: 0 spacing(md);
        width: auto;
      }

      @include mq($from: desktop-wide) {
        margin: auto;
      }

    }

}

.ipl-footer-nav__heading {  
  color: colour(white);
  display: flex;
  margin: spacing(sm) spacing(md);

  @include mq($from: tablet) {
    margin: 0 0 spacing(xs);
  }

  //Secondary titles
  &--secondary {
    margin: spacing(md) 0 0 spacing(-md);

    @include mq($from: tablet) {
      margin: spacing(sm) 0 0;
    }
  }
}
  
.ipl-footer-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 0 spacing(md);

    @include mq($from: tablet) {
      flex-direction: row;
      margin: 0;
    }
}

.ipl-footer-nav__level-one {
  border-bottom: 1px solid colour(grey-500 , .75);
  position: relative;

  @include mq($from: tablet) {
    border: none;
    margin-right: spacing(md);
  }
}

.ipl-footer-nav__toggle {
  background: transparent;
  border: 0;
  cursor: pointer;
  height: 2.5rem;
  left: spacing(sm);
  margin: 0;
  padding: 0;
  position: absolute;
  right: spacing(sm);
  top: 5px;
  transform: translateY(0.375rem);
  width: calc(100% - 2rem);
  z-index: z(header);

  //Active state
  &[aria-expanded="true"] ~ .ipl-footer-nav__list {
    display: flex;
    flex-direction: column;
  }
  &[aria-expanded="true"] .ipl-footer-nav__chevron-down{
    margin-top: 5px;
    transform: translateY(-50%) rotate(180deg);
  }

  &:focus-visible {
    outline: none;
  }


  @include mq($from: tablet) {
    display: none;
  }

}

.ipl-footer-nav__chevron-down {
  color: colour(white);
  bottom: 0;
  font-size: rfs(1.5rem);
  margin-top: spacing(-xxxs);
  max-width: 100%;
  position: absolute;
  right: spacing(sm);
  top: spacing(xs);
  transform: translateY(-13%);

  @include mq($from: tablet) {
    display: none;
  }
}

.ipl-footer-nav__list {
  display: none;
    margin: 0 0 spacing(sm) spacing(md);
    padding: 0;

    @include mq($from: tablet) {
      display: block;
      margin: 0;
    }
}

.ipl-footer-nav__list-item {
    display: block;
    margin: 0;
    padding: spacing(xs) 0; 
    transition: $ipl-transition;

    @include mq($from: tablet) {
      display: block;
      padding: spacing(xxxs) 0; 
    }

    &:first-child {
        margin-left: 0;
        padding-left: 0;
    }

    &:last-child {
        margin-right: 0;
        padding-right: 0;
    }

    .ipl-footer-nav__address {
      color: colour(white);
      display: block;
      font-weight: normal;
    }

}

.ipl-email--sales,
.ipl-social-media {
  display: inline;
  position: relative;
  margin-left: spacing(xl);
}

//links
.ipl-footer-nav__link {
    color: colour(grey-100);
    @include font(primary, regular);
    font-size: rfs(14px);
    text-decoration: none;

    &:hover {
      color: colour(white);
      text-decoration: underline;
      text-decoration-thickness: auto;
      text-decoration-thickness: max(1px,.0625rem);
      text-underline-offset: .4em;
    
      @include mq($from: tablet) {
        &.ipl-footer-nav__link--tel:hover {
          text-decoration: none;
         }
       }
    }
}

.ipl-footer-nav__link--tel {

  @include mq($from: tablet) {
    font-weight: bold;
    text-decoration: none;


    &:focus-visible {
      background-color: transparent;
      box-shadow: none;
      color: colour(white);
      outline: none;
      text-decoration: none;
    }
  }

  @include mq($from: desktop) {
    cursor: auto;
    outline: none;
    box-shadow: none;
    border: none;
  }
}

.ipl-footer-nav__link--title {
  color: colour(white);
  margin-right: spacing(xs);
}

.ipl-footer__bottom {
  border-top: none;
  display: flex;
  flex-wrap: wrap;
  margin: spacing(xs) spacing(sm) 0;
  padding: 0;

  @include mq($from: tablet) {
    border-top: 1px solid colour(grey-500);
    margin: 0;
  }
}

.ipl-footer__bottom--wrapper {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  order: 1;

  @include mq($from: tablet) {
    flex: auto;
    margin: 0 0 0 auto;
    order: 0;
  }

}
.ipl-footer__privacy-link {
  color: colour(white);
  font-size: rfs(14px);
  margin-right: spacing(sm);
  text-decoration: underline;
  text-decoration-thickness: auto;
  text-decoration-thickness: max(1px,.0625rem);
  text-underline-offset: .4em;

  &:hover{
    text-decoration-skip-ink: none;
    text-decoration-thickness: max(3px,.175rem,.12em);
  }
}

.ipl-footer__copyright {
  align-self: center;
  color: colour(white);
  flex: 0 0 100%;
  font-size: rfs(.875rem);
  @include font(primary, regular);
  justify-content: flex-start;
  margin: spacing(sm) 0;

  @include mq($from: tablet) {
    flex: auto;
    margin: 0 0 0 auto;
  }
}

.ipl-footer__iso--wrapper {
  align-items: center;
  display: flex;
  justify-content: left;
  margin: 0 0 spacing(xs) auto;
  padding: 0;


  @include mq($from: tablet) {
    flex: unset;
    justify-content: right;
    margin: spacing(xs) 0 0 auto;
    padding: 0;
  }

  .ipl-footer__iso {
    margin: 0 0 spacing(xxs);
    padding: 0;
  }

  .ipl-logo--iso {
    color:colour(grey-200);
    font-size: 6rem;
    height: 3rem;

    &:hover,
    &:focus-within {
      color:colour(white);
      filter: brightness(150%);
    }

    @include mq($from: tablet) {
      font-size: 8rem;
      height: 4rem;
    }

  }

}

